* {
  margin: 0;
  padding: 0;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  flex-direction: column;
}

.app__titleSection {
  display: flex;
  justify-content: center;
  background-color: #6c5ce7;
  width: 100vw;
  color: white;
  height: 300px;
  align-items: center;
  flex-direction: column;
}

.app__titleSection input {
  text-align: center;
  margin-top: 20px;
  width: 400px;
  height: 40px;
  border-radius: 10px;
  border: none;
  font-size: 20px;
}

.app__titleSection button {
  cursor: pointer;
  border: none;
  margin-top: 30px;
  width: 200px;
  height: 40px;
  border-radius: 10px;
  background-color: #4ca8ee;
  padding-left: 10px;
  font-size: 20px;
  color: white;
  transform: 0.5s ease;
  transform: background-color;
}
.app__titleSection button:hover {
  background-color: #4b36e9;
}
.displayPokemon {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.displayContent {
  margin-left: 20px;
}
.displayContent h3 {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  margin: 5px;

  transition: 0.3s ease;
  transform: color;
}
.image img {
  width: 300px;
  height: 300px;
}

.displayContent h3:hover {
  color: #4b36e9;
}
